import { Component, OnInit } from '@angular/core';
import { StorageService } from 'src/app/storage/main-storage/storage.service';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AlertComponent } from 'src/app/alert/alert.component';
import { ResponseJSON } from 'src/app/storage/main-storage/storage.model';

@Component({
  selector: 'app-storage',
  templateUrl: './storage.component.html',
  styleUrls: ['./storage.component.css']
})
export class StorageComponent implements OnInit {
  sysFiles: Set<string> = new Set();

  constructor(private _storageService: StorageService,
    private _snackBar: MatSnackBar) { }

  ngOnInit() {
    this.onGetFiles();
  }

  onFn(message: string) {
    this._snackBar.openFromComponent(AlertComponent, {
      duration: 3 * 1000,
      data: { message: message, action: 'Action' }
    });
  }

  onGetFiles(): void {
    this._storageService.getStorages()
    .pipe(debounceTime(1000), distinctUntilChanged())
    .subscribe(
      (res: string[]) => {
        for (let name of res) {
          this.sysFiles.add(name);
        }
    });
  }

  onDelete(name: string) {
    this._storageService.deleteFile(name)
    .pipe(debounceTime(1000), distinctUntilChanged())
    .subscribe(
      (res: ResponseJSON) => {
        this.sysFiles.delete(name);
        this.onFn(res.message);
      },
      error => this.onFn(error)
    );
  }

  trackByFn(index, item) {
    return item; // or item.id
  }

}
